import React, { FunctionComponent, useReducer } from "react";

import { initialDiceControlState } from "./state";
import Context from "./context";
import reducer from "./reducer";

const DiceControlProvider: FunctionComponent = ({ children }) => {
  const [controlState, controlDispatch] = useReducer(
    reducer,
    initialDiceControlState,
  );

  return (
    <Context.Provider
      value={{
        controlState,
        controlDispatch,
      }}>
      {children}
    </Context.Provider>
  );
};

export default DiceControlProvider;
