import React, { Dispatch, FC, SetStateAction, useCallback } from "react";

import OptionsButton from "components/OptionsButton";
import { resetFocus } from "utils/resetFocus";
import { fetchBase } from "utils";

import { BoardParamsType } from "./components/Listener/types";
import Controller from "./components/Controller";
import Listener from "./components/Listener";
import Params from "./components/Params";

import DiceControlProvider from "../Reducer";

interface Props {
  setBoardParams: Dispatch<SetStateAction<BoardParamsType>>;
  setIsLiveStatOpen: Dispatch<SetStateAction<boolean>>;
  setIsHotkeyOpen: Dispatch<SetStateAction<boolean>>;
  setIsSoundOpen: Dispatch<SetStateAction<boolean>>;
  setIsBetInfoOpen: () => void;
  isLiveStatOpen: boolean;
  isHotkeyOpen: boolean;
  isSoundOpen: boolean;
}

interface ST {
  socket_token: string;
}

const Panel: FC<Props> = ({
  setIsLiveStatOpen,
  setIsHotkeyOpen,
  setIsSoundOpen,
  setBoardParams,
  setIsBetInfoOpen,
  isLiveStatOpen,
  isHotkeyOpen,
  isSoundOpen,
}) => {
  const handleLiveStatChange = () => {
    setIsLiveStatOpen(!isLiveStatOpen);
  };
  const handleHotKeysChange = () => {
    resetFocus();
    setIsHotkeyOpen(!isHotkeyOpen);
  };
  const handleSoundChange = () => {
    resetFocus();
    setIsSoundOpen(!isSoundOpen);
  };
  const handleTurboDice = useCallback(async () => {
    const turboLink = "https://hyperdice.pasino.com/?token=";
    const { data } = await fetchBase<ST>("account/get-socket-token");
    if (data && data.socket_token) {
      const link = turboLink + data.socket_token;

      window.open(link, "_blank")?.focus();
    }
  }, []);
  return (
    <DiceControlProvider>
      <Listener setBoardParams={setBoardParams}>
        <Controller />
        <OptionsButton
          withSound
          soundOnClick={handleSoundChange}
          liveStatOnclick={handleLiveStatChange}
          hotKeysOnClick={handleHotKeysChange}
          bettingInformationOnClick={setIsBetInfoOpen}
          turboDice={handleTurboDice}
        />

        <Params />
      </Listener>
    </DiceControlProvider>
  );
};

export default Panel;
