import React, { ChangeEvent, FC, FocusEvent, useRef, useState } from "react";
import { useGetLocale } from "providers";

import { useCoins } from "hooks";
import { Button, GetCoinImg } from "components";

import { diceControlConfig as config } from "games/Dice/components/Reducer/config";
import { useHotKeyListener } from "components/HotKeys/Listener";
import { useDiceControl } from "games/Dice/components/Reducer";
import Warning from "components/Warning";

import { Input } from "..";
import css from "./style.module.scss";

const Amount: FC = () => {
  const intl = useGetLocale();
  const { controlState, controlDispatch } = useDiceControl();
  const { showInUSD } = useCoins("showInUSD");

  const [warning, setWarning] = useState(false);
  const buttonBoxRef = useRef<HTMLDivElement>(null);

  const openWarning = () => {
    setWarning(true);
  };
  const closeWarning = () => {
    setWarning(false);
  };

  const handleChangeAmount = ({ target }: ChangeEvent<HTMLInputElement>) => {
    controlDispatch({
      type: "changeValue",
      value: {
        element: "mainBet",
        amount: target.value,
      },
    });
  };

  const handleBlurAmount = ({ target }: FocusEvent<HTMLInputElement>) => {
    let targetAmount = Number(target.value);
    if (targetAmount > Number(controlState.general.activeCoin.value)) {
      targetAmount = Number(
        controlState.general.coinsConfig.selectedCoin.minimum_bet,
      );
    }

    if (
      targetAmount <
      Number(controlState.general.coinsConfig.selectedCoin.minimum_bet)
    ) {
      targetAmount = Number(
        controlState.general.coinsConfig.selectedCoin.minimum_bet,
      );
    }

    controlDispatch({
      type: "changeValue",
      value: {
        element: "mainBet",
        amount: targetAmount.toFixed(config.accuracy),
      },
    });

    localStorage.setItem(
      config.localNames.amount,
      targetAmount.toFixed(config.accuracy),
    );
  };

  const handleAmountDivide = () => {
    const amount = Number(controlState.panel.main.betAmount);
    let result = amount / 2;

    if (result > Number(controlState.general.activeCoin.value)) {
      result = Number(controlState.general.activeCoin.value);
    }

    if (
      result < Number(controlState.general.coinsConfig.selectedCoin.minimum_bet)
    ) {
      result = Number(
        controlState.general.coinsConfig.selectedCoin.minimum_bet,
      );
    }

    controlDispatch({
      type: "changeValue",
      value: {
        element: "mainBet",
        amount: result.toFixed(config.accuracy),
      },
    });
  };

  const handleAmountMultiply = () => {
    const amount = Number(controlState.panel.main.betAmount);
    let result = amount * 2;

    if (result > Number(controlState.general.activeCoin.value)) {
      result = Number(controlState.general.activeCoin.value);
    }

    if (
      result > Number(controlState.general.coinsConfig.selectedCoin.maximum_bet)
    ) {
      result = Number(
        controlState.general.coinsConfig.selectedCoin.maximum_bet,
      );
    }

    controlDispatch({
      type: "changeValue",
      value: {
        element: "mainBet",
        amount: result.toFixed(config.accuracy),
      },
    });
  };

  const handleAmountMax = () => {
    let result = Number(
      controlState.general.coinsConfig.selectedCoin.maximum_bet,
    );
    if (result > Number(controlState.general.activeCoin.value)) {
      result = Number(controlState.general.activeCoin.value);
    }

    controlDispatch({
      type: "changeValue",
      value: {
        element: "mainBet",
        amount: result.toFixed(config.accuracy),
      },
    });
  };

  const handleAmountMin = () => {
    let result = Number(
      controlState.general.coinsConfig.selectedCoin.minimum_bet,
    );

    controlDispatch({
      type: "changeValue",
      value: {
        element: "mainBet",
        amount: result.toFixed(config.accuracy),
      },
    });
  };

  useHotKeyListener({
    callbackKeyA: handleAmountDivide,
    callbackKeyS: handleAmountMultiply,
  });

  return (
    <div className={css.box}>
      <div className={css.inputWrapper}>
        <Input
          withCoin
          value={controlState.panel.main.betAmount}
          onChange={handleChangeAmount}
          onBlur={handleBlurAmount}
          disabled={controlState.auto.isStarted}
        />
        <GetCoinImg className={css.coinImg} />
        {showInUSD && controlState.general.activeCoin.name !== "GEM" ? (
          <span className={css.dollarLabel}>$</span>
        ) : null}
      </div>
      <div className={css.buttonsBox} ref={buttonBoxRef}>
        <Button className={css.button} onClick={handleAmountMin}>
          {intl.formatMessage({ id: "crashInterface.bet.menu.buttons.min" })}
        </Button>
        <Button className={css.button} onClick={handleAmountDivide}>
          {intl.formatMessage({ id: "crashInterface.bet.menu.buttons.half" })}
        </Button>
        <Button className={css.button} onClick={handleAmountMultiply}>
          {intl.formatMessage({
            id: "crashInterface.bet.menu.buttons.double",
          })}
        </Button>
        <Button className={css.button} onClick={openWarning}>
          {intl.formatMessage({ id: "crashInterface.bet.menu.buttons.max" })}
        </Button>

        <Warning
          isOpen={warning}
          onClose={closeWarning}
          callBack={handleAmountMax}
        />
      </div>
    </div>
  );
};

export default Amount;
