import { diceControlConfig as config } from "../config";
import { DiceControlActions, DiceControlState } from "../types";
import {
  calculateChance,
  calculateOdds,
  calculateProfit,
  calculateRange,
} from "./calculates";

export default function setPanelMainValues(
  state: DiceControlState,
  action: DiceControlActions,
) {
  const actualAmount = Number(state.panel.main.betAmount);

  switch (action.type) {
    case "changeCoinConfigReaction":
      let newAmount = actualAmount.toFixed(config.accuracy);

      if (!actualAmount) {
        newAmount = state.general.coinsConfig.selectedCoin.minimum_bet;
      }
      if (state.general.coinsConfig.selectedCoin.coin) {
        if (
          actualAmount <
          Number(state.general.coinsConfig.selectedCoin.minimum_bet)
        ) {
          newAmount = Number(
            state.general.coinsConfig.selectedCoin.minimum_bet,
          ).toFixed(config.accuracy);
        }
        if (
          actualAmount >
          Number(state.general.coinsConfig.selectedCoin.maximum_bet)
        ) {
          newAmount = Number(
            state.general.coinsConfig.selectedCoin.maximum_bet,
          ).toFixed(config.accuracy);
        }
      }
      return {
        ...state.panel.main,
        betAmount: newAmount,
        profit: calculateProfit(newAmount, state.panel.main.odds),
      };

    case "changeValue":
      switch (action.value.element) {
        case "mainBet":
          localStorage.setItem(config.localNames.amount, action.value.amount);

          return {
            ...state.panel.main,
            betAmount: action.value.amount,
            profit: calculateProfit(action.value.amount, state.panel.main.odds),
          };

        case "odds":
          const chance = calculateChance(action.value.amount);
          return {
            ...state.panel.main,
            odds: action.value.amount,
            chance,
            profit: calculateProfit(
              state.panel.main.betAmount,
              action.value.amount,
            ),
            range: calculateRange(chance),
          };

        case "chance":
          return {
            ...state.panel.main,
            chance: action.value.amount,
            odds: calculateOdds(action.value.amount),
            profit: calculateProfit(
              state.panel.main.betAmount,
              state.panel.main.odds,
            ),
            range: calculateRange(action.value.amount),
          };

        default:
          break;
      }
      break;
    case "calculateOdds":
      const oldOddsFix =
        Number(state.panel.main.odds) > config.odds.max
          ? config.odds.max
          : Number(state.panel.main.odds) < config.odds.min
          ? config.odds.min
          : state.panel.main.odds;

      const chance = calculateChance(String(oldOddsFix));
      const odds = calculateOdds(chance);

      localStorage.setItem(config.localNames.odds, odds);
      localStorage.setItem(config.localNames.chance, chance);
      return {
        ...state.panel.main,
        chance,
        odds,
        profit: calculateProfit(state.panel.main.betAmount, odds),
        range: calculateRange(chance),
      };
    default:
      break;
  }
  return state.panel.main;
}
