import React, { ChangeEvent } from "react";
import { ReactComponent as AllInclusiveIcon } from "assets/icons/all-inclusive.svg";
import { useGetLocale } from "providers";
import clsx from "clsx";

import { useDiceControl } from "games/Dice/components/Reducer";

import Input from "../../Input";
import css from "./style.module.scss";

export const NumberOfBets = () => {
  const intl = useGetLocale();
  const { controlState, controlDispatch } = useDiceControl();

  const handleNumberOfBetsChange = ({
    target,
  }: ChangeEvent<HTMLInputElement>) => {
    let value = target.value;
    if (value === "") {
      value = "0";
    }

    if (value === "0" || Number(value)) {
      controlDispatch({
        type: "changeNumberOfBets",
        value: Number(value),
      });
    }
  };

  const handleNumberOfBetsBlur = () => {
    let value = Number(controlState.auto.numberOfBets.expected);

    if (value < 0) {
      value *= -1;
    }
    controlDispatch({
      type: "changeNumberOfBets",
      value,
    });
  };

  return (
    <div className={css.root}>
      <h1 className={css.title}>
        {intl.formatMessage({ id: "dice.auto.roll.numberOfBets" })}
      </h1>
      <div className={css.inputWrapper}>
        <Input
          type="number"
          className={clsx(css.betsCountInput, {
            [css.betsCountInputPadding]:
              controlState.auto.numberOfBets.expected === 0,
          })}
          value={
            !controlState.auto.isStarted ||
            controlState.auto.numberOfBets.expected === 0
              ? controlState.auto.numberOfBets.expected
              : controlState.auto.numberOfBets.expected -
                controlState.auto.numberOfBets.current
          }
          onChange={handleNumberOfBetsChange}
          onBlur={handleNumberOfBetsBlur}
        />
        {controlState.auto.numberOfBets.expected === 0 && (
          <AllInclusiveIcon className={css.infinityIcon} />
        )}
      </div>
    </div>
  );
};
