import React, { FC, ChangeEvent } from "react";

import { useCoins } from "hooks";
import { useDiceControl } from "games/Dice/components/Reducer";

import Input from "../../Input";
import css from "./style.module.scss";

export const StopOn: FC = () => {
  const { showInUSD } = useCoins("showInUSD");
  const { controlDispatch, controlState } = useDiceControl();
  const { stopOnLoss, stopOnProfit } = controlState.auto;

  const inputOnChangeHandler = (
    event: ChangeEvent<HTMLInputElement>,
    field: "profit" | "loss",
  ) => {
    const value = showInUSD
      ? event.target.value.replace("$", "")
      : event.target.value;
    const checkingValue = Number(value);
    if (isNaN(checkingValue) || !isFinite(checkingValue) || checkingValue < 0) {
      return;
    }
    controlDispatch({
      type: field === "profit" ? "setStopOnProfit" : "setStopOnLoss",
      value,
    });
  };

  return (
    <div className={css.root}>
      <h1>Stop On Profit</h1>
      <h1>Stop On Loss</h1>
      <div className={css.inputsWrapper}>
        <Input
          value={`${showInUSD ? "$" : ""}${stopOnProfit}`}
          type="text"
          onChange={(event) => inputOnChangeHandler(event, "profit")}
          disabled={controlState.auto.isStarted}
        />
        <Input
          value={`${showInUSD ? "$" : ""}${stopOnLoss}`}
          type="text"
          onChange={(event) => inputOnChangeHandler(event, "loss")}
          disabled={controlState.auto.isStarted}
        />
      </div>
    </div>
  );
};

export default StopOn;
