import { Decimal } from "utils/decimal";
import { toDecimal, replaceNumbers } from "utils";

import { diceControlConfig as config } from "../config";

const fixedValue = 15;

const splice = (result: number, end: number) => {
  const num = result.toFixed(fixedValue).split(".");
  const [integer, frag] = num;

  return replaceNumbers(`${integer || 0}.${frag?.slice(0, end) || "0"}`);
};

export const calculateRange = (chance: string) => {
  const maxValue = 9999,
    minValue = 0;

  let rangeValue = Math.round(Number(chance) * 100);
  if (rangeValue > maxValue) rangeValue = maxValue;
  if (rangeValue < minValue) rangeValue = minValue;

  return {
    min: rangeValue,
    max: Math.round(maxValue - rangeValue),
  };
};

export const calculateChance = (payout: string) => {
  let value = Number(payout);

  if (!value || value < 0 || value === Infinity) {
    value = 0;
  }
  if (value < config.odds.min) {
    value = config.odds.min;
  } else if (value > config.odds.max) {
    value = config.odds.max;
  }

  const floatVar = value;
  const result = toDecimal(config.odds.max)
    .div(floatVar)
    .div(100)
    .toDecimalPlaces(2, Decimal.ROUND_DOWN);

  return String(result);
};

export const calculateProfit = (amount: string, odds: string) => {
  const newAmount = toDecimal(amount === "" ? 0 : amount);

  let result = newAmount
    .mul(odds || "0")
    .minus(newAmount)
    .toDecimalPlaces(fixedValue);

  return result.toFixed(result.dp());
};

export const calculateOdds = (chance: string) => {
  const winning_chance = Number(chance);
  let result = 95 / Number(winning_chance);

  if (result > config.odds.max) {
    result = config.odds.max;
  } else if (result < config.odds.min) {
    result = config.odds.min;
  }

  return splice(result, 5);
};
