import React, { useLayoutEffect, useRef } from "react";
import Roll from "digit-roll-react";
import clsx from "clsx";

import { scrollAdder } from "./addScrollElement";
import "./style.css";

const DigitRoll = React.memo(Roll);

export const Roller = ({ num, isFail, isAuto }) => {
  const rollerBox = useRef(null);

  useLayoutEffect(() => {
    if (rollerBox.current) {
      scrollAdder(rollerBox.current);
    }
  }, [rollerBox, num]);
  console.log(num);
  return (
    <div className="Roller__Box" ref={rollerBox}>
      <DigitRoll
        num={num}
        length={4}
        divider=""
        delay={!isAuto ? "1" : "0"}
        height={9}
        width={7.5}
        className={clsx({
          ["textFail"]: isFail,
          ["autoOn"]: isAuto,
        })}
      />
    </div>
  );
};

export default React.memo(Roller);
