import React, {
  Dispatch,
  FC,
  SetStateAction,
  useEffect,
  useState,
} from "react";

import { coinsManager, useCoins, useFetch } from "hooks";
import { useDiceControl } from "games/Dice/components/Reducer";

import { BoardParamsType, CoinConfigFetch, CoinConfig } from "./types";

interface Props {
  setBoardParams: Dispatch<SetStateAction<BoardParamsType>>;
}

const Listener: FC<Props> = ({ children, setBoardParams }) => {
  const { controlState, controlDispatch } = useDiceControl();
  const { selectedCoin, showInUSD, coinsPrices } = useCoins();
  const [coinConfigFetch, { data, called, loading }] =
    useFetch<CoinConfigFetch>("/dice/get-configuration");
  const [isInit, setInit] = useState(false);

  const calculateMinCeil = (value: string, price: string) => {
    return (+(
      Math.ceil(Number(value) * Number(price) * 100000000) / 100000000
    )).toFixed(8);
  };

  useEffect(() => {
    controlDispatch({
      type: "clear",
    });
    coinConfigFetch();
  }, []);

  useEffect(() => {
    if (selectedCoin && coinsManager.selectedCoin) {
      let balance = coinsManager.selectedCoin.balance;
      if (showInUSD && selectedCoin.coin !== "GEM") {
        balance = (
          Number(selectedCoin.balance) *
          Number(coinsPrices[selectedCoin.coin]?.price)
        ).toString();
      }
      controlDispatch({
        type: "setSelectedCoin",
        coin: {
          name: selectedCoin.coin,
          value: balance,
        },
      });

      if (called && !loading) {
        controlDispatch({
          type: "changeCoinData",
          newCoin: selectedCoin.coin,
        });
      }
    }
  }, [selectedCoin?.coin, showInUSD]);

  useEffect(() => {
    if (data) {
      const coins: CoinConfig[] = [];

      data.coins.forEach((item) => {
        if (showInUSD && item.coin !== "GEM") {
          coins.push({
            coin: item.coin,
            minimum_bet: calculateMinCeil(
              item.minimum_bet,
              coinsPrices[item.coin]?.price,
            ),
            maximum_bet: (
              Number(item.maximum_bet) * Number(coinsPrices[item.coin]?.price)
            ).toString(),
            maximum_profit: (
              Number(item.maximum_profit) *
              Number(coinsPrices[item.coin]?.price)
            ).toString(),
            minimum_bet_orig: item.minimum_bet,
          });
        } else {
          coins.push({
            coin: item.coin,
            minimum_bet: item.minimum_bet,
            maximum_bet: item.maximum_bet,
            maximum_profit: item.maximum_profit,
            minimum_bet_orig: item.minimum_bet,
          });
        }
      });
      controlDispatch({
        type: "setGeneralCoinData",
        coins: coins,
      });
    }
  }, [data, showInUSD, coinsPrices]);

  useEffect(() => {
    controlDispatch({
      type: "changeCoinConfigReaction",
    });
  }, [controlState.general.coinsConfig.selectedCoin]);

  useEffect(() => {
    const board = controlState.general.board;
    setBoardParams({
      value: board.value,
      type: board.type,
      isFail: board.isFail,
      isAuto: controlState.auto.isOpen,
      range: controlState.panel.main.range,
    });
  }, [
    controlState.general.board,
    controlState.auto.isOpen,
    controlState.panel.main.range,
  ]);

  useEffect(() => {
    if (!isInit) {
      setInit(true);
      return;
    }
    if (
      !controlState.general.activeCoin.name ||
      controlState.general.activeCoin.name === "GEM"
    ) {
      return;
    }
    let bet: string;
    let min = controlState.general.coinsConfig.selectedCoin.minimum_bet_orig;

    if (showInUSD) {
      bet = (
        Number(controlState.panel.main.betAmount) *
        Number(coinsPrices[controlState.general.activeCoin.name]?.price)
      ).toFixed(8);
      min = calculateMinCeil(
        controlState.general.coinsConfig.selectedCoin.minimum_bet_orig,
        coinsPrices[controlState.general.activeCoin.name]?.price,
      );
    } else {
      bet = (
        Number(controlState.panel.main.betAmount) /
        Number(coinsPrices[controlState.general.activeCoin.name]?.price)
      ).toFixed(8);
    }

    controlDispatch({
      type: "changeValue",
      value: {
        element: "mainBet",
        amount: Number(min) > Number(bet) ? min : bet,
      },
    });
  }, [showInUSD]);

  return <>{children}</>;
};

export default Listener;
