import { DiceControlActions, DiceControlState } from "../types";

export default function setActiveCoin(
  state: DiceControlState,
  action: DiceControlActions,
) {
  switch (action.type) {
    case "setSelectedCoin":
      return {
        name: action.coin.name,
        value: action.coin.value,
      };

    default:
      break;
  }
  return state.general.activeCoin;
}
