import React, {
  ChangeEventHandler,
  FC,
  FocusEventHandler,
  useMemo,
} from "react";
import clsx from "clsx";

import css from "./style.module.scss";

interface Props {
  value?: string | number;
  onBlur?: FocusEventHandler<HTMLInputElement>;
  onChange?: ChangeEventHandler<HTMLInputElement>;
  type?: "text" | "number";
  className?: string;
  withCoin?: boolean;
  disabled?: boolean;
}

const SimpleInput: FC<Props> = ({
  onBlur,
  onChange,
  value,
  type = "number",
  className,
  withCoin,
  disabled,
}) => {
  const coinPadding = useMemo(
    () => ({
      paddingLeft: withCoin ? 35 : 12,
    }),
    [withCoin],
  );
  return (
    <input
      className={clsx(css.input, className)}
      onBlur={onBlur}
      value={value}
      onChange={onChange}
      type={type}
      style={coinPadding}
      disabled={disabled}
    />
  );
};

export default SimpleInput;
