import React from "react";

import { Helmet } from "react-helmet";

import SeedProvider from "providers/Seed";
import "config/themes/_dice.scss";

import DicePage from "./page";

const DiceWrapper = () => {
  return (
    <SeedProvider gameName="dice">
      <Helmet>
        <title>HashDice | Crypto Gambling Casino</title>
        <meta name="title" content="Play Bitcoin HashDice - Pasino.com" />
        <meta
          name="description"
          content="The most popular dice game for bitcoin is back. Start to play the HashDice with DOGE, ETH, Litecoin, and many other cryptocurrencies on pasino.com."
        />
        <meta
          name="keywords"
          content="Dice game, dice Betting, Dice Gambling, Pasino Dice, Bitcoin Dice game, Litecoin dice game, primedice, freebitcoin, "
        />
        <meta name="robots" content="index, follow" />
        <meta http-equiv="Content-Type" content="text/html; charset=utf-8" />
        <meta name="language" content="English" />
      </Helmet>

      <DicePage />
    </SeedProvider>
  );
};

export default DiceWrapper;
