import React, { FC, useEffect, useMemo } from "react";

import { coinsManager, useCoins, useLocalStorage } from "hooks";
import { SwitchBox, notifyBarManager, Button } from "components";
import { useGetLocale } from "providers";
import { fetchBase, sleep } from "utils";
import { useSeed } from "providers";

import { liveStatsManager } from "components/LiveStats/eventEmitter/emitter";
import { useSoundSystemControl } from "components/SoundModal/Provider";
import { useHotKeyListener } from "components/HotKeys/Listener";
import { useDiceControl } from "games/Dice/components/Reducer";
import {
  gamePathNames,
  getSoundEffectNames,
} from "components/SoundModal/Provider/config";

import { calculateProfit } from "../../../Reducer/actions/calculates";
import { PlayType } from "../Listener/types";
import css from "./style.module.scss";

const Controller: FC = () => {
  const intl = useGetLocale();
  const { controlState, controlDispatch } = useDiceControl();
  const { clientSeed, rotateClientSeed, updateNonce } = useSeed();
  const [token] = useLocalStorage("token");
  const { showInUSD, coinsPrices } = useCoins(["showInUSD", "coinsPrices"]);

  const {
    controlFunctions: { playEffect },
  } = useSoundSystemControl();
  const { hashDice } = getSoundEffectNames(gamePathNames.DICE);

  const handleAuto = (isAuto: boolean) => {
    controlDispatch({
      type: "changeAuto",
      value: isAuto,
    });
  };

  const send = async () => {
    if (clientSeed) {
      let bet_amt = controlState.panel.main.betAmount;
      let profit = controlState.panel.main.profit;
      if (showInUSD && controlState.general.activeCoin.name !== "GEM") {
        bet_amt = (
          Number(controlState.panel.main.betAmount) /
          Number(coinsPrices[controlState.general.activeCoin.name]?.price)
        ).toFixed(8);
        profit = calculateProfit(bet_amt, controlState.panel.main.odds);
      }
      const send = {
        token,
        bet_amt: bet_amt,
        coin: controlState.general.activeCoin.name,
        type: controlState.general.board.type,

        payout: controlState.panel.main.odds,
        winning_chance: controlState.panel.main.chance,

        profit: profit,
        client_seed: clientSeed?.seed,
      };
      await sleep(1 / 5); // 5bet/second

      const fetch = await fetchBase<PlayType>("dice/play", {
        variables: send,
      });

      if (fetch.error) {
        notifyBarManager.error({
          timeOut: 4000,
          message: fetch.error,
        });
        controlDispatch({
          type: "autoError",
        });

        controlDispatch({
          type: "boardInstall",
          value: {
            balance: coinsManager.selectedCoin?.balance || "0",
            coin: coinsManager.selectedCoin?.coin || "gem",
            roll_number: 0,
            win: 1,
            profit,
          },
        });
      }

      if (fetch.data?.success) {
        const { balance, coin, profit, roll_number, win } = fetch.data;
        let bal = balance;

        coinsManager.coinValueUpdate(balance, coin);

        if (showInUSD && coin !== "GEM") {
          bal = (Number(balance) * Number(coinsPrices[coin]?.price)).toString();
        }

        const permissionLiveStats = controlState.auto.isOpen
          ? controlState.auto.isStarted
          : true;

        if (permissionLiveStats) {
          liveStatsManager.addBet({
            profit: Number(profit),
            amount: Number(controlState.panel.main.betAmount),
            coin,
          });
        }

        controlDispatch({
          type: "boardInstall",
          value: {
            balance: bal,
            coin,
            roll_number,
            win,
            profit,
          },
        });
      }

      debugger;

      if (!clientSeed?.isSetManually) {
        rotateClientSeed();
      } else {
        updateNonce();
      }
    }
  };

  const play = () => {
    if (token) {
      playEffect(hashDice);
      if (!controlState.auto.isOpen) {
        send();
      } else {
        controlDispatch({
          type: "autoChange",
        });
      }
    } else {
      notifyBarManager.error({
        timeOut: 4000,
        message: intl.formatMessage({ id: "crashInterface.messages.noLogin" }),
      });
    }
  };

  const typeChange = (enable: boolean) =>
    controlDispatch({
      type: "changeGameType",
      value: enable ? 1 : 2,
    });

  const switchText = useMemo(
    () => ({
      type: {
        off: intl.formatMessage({
          id: "plinko.risk.high",
        }),
        on: intl.formatMessage({
          id: "plinko.risk.low",
        }),
      },
      auto: {
        on: intl.formatMessage({
          id: "crashInterface.bet.switch.auto",
        }),
        off: intl.formatMessage({
          id: "crashInterface.bet.switch.manual",
        }),
      },
    }),
    [],
  );

  const buttonStatus = useMemo(() => {
    if (controlState.auto.isOpen) {
      if (controlState.auto.isStarted) {
        return intl.formatMessage({
          id: "crashInterface.bet.button.autoStop",
        });
      } else {
        return intl.formatMessage({
          id: "crashInterface.bet.button.autoStart",
        });
      }
    } else {
      return intl.formatMessage({
        id: "crashInterface.bet.button.placeABet",
      });
    }
  }, [controlState.auto.isOpen, controlState.auto.isStarted]);

  useEffect(() => {
    if (controlState.auto.isStarted) {
      if (controlState.auto.isReady) {
        send();
        controlDispatch({
          type: "autoPlayed",
        });
      }
    } else {
      controlState.auto.isReady = false;
    }
  }, [controlState.auto.isReady]);

  useHotKeyListener({
    callbackSpace: play,
  });

  return (
    <div className={css.controlBox}>
      <div className={css.switch_1}>
        <SwitchBox
          text={switchText.type}
          isChecked={controlState.general.board.type === 1}
          switchHandleChange={typeChange}
          toggleSwitchPosition="65%"
        />
      </div>

      <div className={css.buttonArea}>
        <Button
          className={css.button}
          onClick={play}
          disabled={false}
          waveColor={"black"}>
          <div className={css.buttonWrapper}>
            <p className={css.buttonText}>{buttonStatus}</p>
          </div>
        </Button>
      </div>
      <div className={css.switch_2}>
        <SwitchBox
          text={switchText.auto}
          isChecked={controlState.auto.isOpen}
          switchHandleChange={handleAuto}
          toggleSwitchPosition="65%"
        />
      </div>
    </div>
  );
};

export default Controller;
