import React, { ChangeEvent, FocusEvent, useMemo } from "react";
import clsx from "clsx";

import { useGetLocale } from "providers";
import { RadioGroup } from "components";
import Checkbox from "components/Checkbox";
import { toDecimal, Decimal } from "utils";

import { replaceNumbers } from "utils/replaceString";
import { OnDecisionProps } from "games/Dice/types";
import { useDiceControl } from "games/Dice/components/Reducer";
import { diceControlConfig as config } from "games/Dice/components/Reducer/config";
import {
  calculateChance,
  calculateOdds,
} from "games/Dice/components/Reducer/actions";

import Input from "../../Input";
import css from "./style.module.scss";
interface DecisionsProps {
  type: "win" | "lose";
}

type changeInput = ChangeEvent<HTMLInputElement>;
type focusInput = FocusEvent<HTMLInputElement>;

export const Decisions = ({ type }: DecisionsProps) => {
  const intl = useGetLocale();

  const { controlState, controlDispatch } = useDiceControl();
  const decisionsState = useMemo(
    () => controlState.auto.decisions[type],
    [controlState.auto.decisions[type]],
  );
  const handleChange = (value: OnDecisionProps) => {
    controlDispatch({
      type: "changeDecisions",
      element: type,
      value,
    });
  };

  const handleRadioChange = (ev: any) => {
    handleChange({
      ...decisionsState,
      variant: Number(ev.target.value),
    });
  };
  const handleCheckBoxChange = (ev: any, checked: boolean) => {
    handleChange({
      ...decisionsState,
      isCashout: checked,
    });
  };
  const handleChangeIncreaseBetPercent = ({ target }: changeInput) => {
    handleChange({
      ...decisionsState,
      betPercent: target.value,
    });
  };
  const handleCashoutChange = ({ target }: changeInput) => {
    handleChange({
      ...decisionsState,
      cashout: target.value,
      percent: calculateChance(target.value),
    });
  };
  const handleChanceChange = ({ target }: changeInput) => {
    handleChange({
      ...decisionsState,
      percent: target.value,
      cashout: calculateOdds(target.value),
    });
  };

  const handleBlurIncreaseBetPercent = ({ target }: focusInput) => {
    handleChange({
      ...decisionsState,
      betPercent: replaceNumbers(Number(target.value).toFixed(2)),
    });
  };
  const handleCashoutBlur = () => {
    handleChange({
      ...decisionsState,
      cashout: replaceNumbers(calculateOdds(decisionsState.percent)),
    });
  };
  const handleChanceBlur = ({ target }: focusInput) => {
    let percent = Number(
      toDecimal(target.value).toDecimalPlaces(2, Decimal.ROUND_DOWN),
    );

    if (percent < config.chance.min) {
      percent = config.chance.min;
    }
    if (percent > config.chance.max) {
      percent = config.chance.max;
    }

    handleChange({
      ...decisionsState,
      percent: replaceNumbers(String(percent)),
      cashout: replaceNumbers(calculateOdds(String(percent))),
    });
  };

  const radioStyle = { label: css.label };
  const checkboxStyle = { label: css.label, root: css.checkBox };

  return (
    <div className={css.root}>
      <h1 className={css.title}>
        {intl.formatMessage({
          id:
            type === "win"
              ? "dice.auto.decisions.onWin"
              : "dice.auto.decisions.onLoss",
        })}
      </h1>
      <fieldset className={css.pnlOptions}>
        <RadioGroup
          value={decisionsState.variant}
          values={[0, 1, 2]}
          labels={[
            intl.formatMessage({
              id: "dice.auto.decisions.stopBetBy",
            }),
            intl.formatMessage({
              id: "dice.auto.decisions.returnToBase",
            }),
            intl.formatMessage({
              id: "dice.auto.decisions.increase",
            }),
          ]}
          onChange={handleRadioChange}
          name={`${type}_radiogroup`}
          nestedComponents={{
            3: (
              <div className={css.inputWrapper}>
                <div className={css.percentWrapper}>
                  <Input
                    type="number"
                    className={clsx(css.inputUnit, css.inputPercent)}
                    onChange={handleChangeIncreaseBetPercent}
                    value={decisionsState.betPercent}
                    onBlur={handleBlurIncreaseBetPercent}
                  />
                </div>
              </div>
            ),
          }}
          radioClasses={{
            label: css.radioLabel,
            round: css.radioRound,
            radio: css.radioRadio,
            root: css.radioRoot,
          }}
        />

        <Checkbox
          classes={checkboxStyle}
          onChange={handleCheckBoxChange}
          className={css.check}
          checked={decisionsState.isCashout}
          name="changeCashout"
          label={intl.formatMessage({
            id: "dice.auto.decisions.change",
          })}
        />

        <div className={css.inputsBox}>
          <Input
            type="number"
            value={decisionsState.cashout}
            className={css.inputUnit}
            onBlur={handleCashoutBlur}
            onChange={handleCashoutChange}
          />

          <div className={css.percentWrapper}>
            <Input
              type="number"
              value={decisionsState.percent}
              className={clsx(css.inputUnit, css.inputPercent)}
              onBlur={handleChanceBlur}
              onChange={handleChanceChange}
            />
          </div>
        </div>
      </fieldset>
    </div>
  );
};
