import {
  autoResponse,
  setActiveCoin,
  setCoinConfig,
  setPanelMainValues,
} from "./actions";
import { diceControlConfig as config } from "./config";
import { DiceControlActions, DiceControlState } from "./types";

export const diceControlReducer = (
  state: DiceControlState,
  action: DiceControlActions,
): DiceControlState => {
  switch (action.type) {
    case "changeCoinData":
    case "setGeneralCoinData":
      return {
        ...state,
        general: {
          ...state.general,
          coinsConfig: setCoinConfig(state, action),
        },
        panel: {
          ...state.panel,
          main: {
            ...state.panel.main,
          },
        },
      };
    case "setSelectedCoin":
      state.auto.isStarted = false;
      return {
        ...state,
        general: {
          ...state.general,
          activeCoin: setActiveCoin(state, action),
        },
      };
    case "changeCoinConfigReaction":
    case "calculateOdds":
    case "changeValue":
      return {
        ...state,
        panel: {
          ...state.panel,
          main: setPanelMainValues(state, action),
        },
      };

    case "boardInstall":
      const { balance, win, coin, roll_number, profit } = action.value;

      return {
        ...autoResponse(state, action, profit),
        general: {
          ...state.general,
          activeCoin: {
            name: coin,
            value: balance,
          },
          board: {
            ...state.general.board,
            isFail: !win,
            value: String(roll_number),
          },
        },
      };

    case "changeGameType":
      return {
        ...state,
        general: {
          ...state.general,
          board: {
            ...state.general.board,
            type: action.value,
          },
        },
      };

    case "changeAuto":
      return {
        ...state,
        auto: {
          ...state.auto,
          isOpen: action.value,
        },
      };

    case "changeDecisions":
      return {
        ...state,
        auto: {
          ...state.auto,
          decisions: {
            ...state.auto.decisions,
            [action.element]: action.value,
          },
        },
      };

    case "setSelectTypeMode":
      return {
        ...state,
        auto: {
          ...state.auto,
          selectType: action.value,
        },
      };

    case "changeNumberOfBets":
      return {
        ...state,
        auto: {
          ...state.auto,
          numberOfBets: {
            ...state.auto.numberOfBets,
            expected: action.value,
          },
        },
      };

    case "autoChange":
      const autoEnable = !state.auto.isStarted;
      return {
        ...state,
        auto: {
          ...state.auto,
          numberOfBets: {
            ...state.auto.numberOfBets,
            current: 1,
          },
          isStarted: autoEnable,
          isReady: autoEnable ? true : false,
          baseBet: autoEnable ? state.panel.main.betAmount : "",
        },
        panel: {
          ...state.panel,
          main: {
            ...state.panel.main,
            betAmount: !autoEnable
              ? state.auto.baseBet
              : state.panel.main.betAmount,
          },
        },
      };
    case "autoPlayed":
      return {
        ...state,
        auto: {
          ...state.auto,
          isReady: false,
        },
      };
    case "autoError":
      return {
        ...state,
        auto: {
          ...state.auto,
          isStarted: false,
          isReady: false,
        },
      };
    case "clear":
      return {
        ...state,
        panel: {
          ...state.panel,
          main: {
            ...state.panel.main,
            betAmount:
              localStorage.getItem(config.localNames.amount) ||
              (0).toFixed(config.accuracy),
            chance:
              localStorage.getItem(config.localNames.chance) ||
              (47.5).toFixed(config.accuracy),
            odds:
              localStorage.getItem(config.localNames.odds) || (2).toFixed(5),
          },
        },
      };

    case "setStopOnLoss":
      return {
        ...state,
        auto: {
          ...state.auto,
          stopOnLoss: action.value,
        },
      };

    case "setStopOnProfit":
      return {
        ...state,
        auto: {
          ...state.auto,
          stopOnProfit: action.value,
        },
      };

    case "setPnl":
      return {
        ...state,
        auto: {
          ...state.auto,
          pnl: action.value,
        },
      };

    default:
      return state;
  }
};

export default diceControlReducer;
