import React, { ChangeEvent } from "react";
import { useGetLocale } from "providers";

import { RadioGroup } from "components";
import { useDiceControl } from "games/Dice/components/Reducer";

import css from "./style.module.scss";

export const SelectType = () => {
  const intl = useGetLocale();

  const { controlState, controlDispatch } = useDiceControl();

  const handleRadioChange = ({ target }: ChangeEvent<HTMLInputElement>) => {
    controlDispatch({
      type: "setSelectTypeMode",
      value: target.value,
    });
  };

  return (
    <fieldset className={css.root}>
      <h1 className={css.title}>
        {intl.formatMessage({ id: "dice.auto.select.title" })}
      </h1>

      <RadioGroup
        value={controlState.auto.selectType}
        values={["0", "1", "2", "3"]}
        name="select_type_radiogroup"
        onChange={handleRadioChange}
        labels={[
          intl.formatMessage({
            id: "dice.auto.select.winSwitch",
          }),
          intl.formatMessage({
            id: "dice.auto.select.lossSwitch",
          }),
          intl.formatMessage({
            id: "dice.auto.select.betSwitch",
          }),
          intl.formatMessage({
            id: "dice.auto.select.noSwitch",
          }),
        ]}
        radioClasses={{
          label: css.radioLabel,
          round: css.radioRound,
          radio: css.radioRadio,
          root: css.radioRoot,
        }}
      />
    </fieldset>
  );
};
