import React, { ChangeEvent, FC } from "react";
import { useGetLocale } from "providers";
import clsx from "clsx";

import { useCoins } from "hooks";
import { GetCoinImg } from "components";

import { useDiceControl } from "games/Dice/components/Reducer";

import { Amount, Input } from "..";
import css from "./style.module.scss";

const Manual: FC = () => {
  const intl = useGetLocale();
  const { controlState, controlDispatch } = useDiceControl();
  const { showInUSD } = useCoins("showInUSD");

  const handleChangeOdds = ({ target }: ChangeEvent<HTMLInputElement>) => {
    controlDispatch({
      type: "changeValue",
      value: {
        element: "odds",
        amount: target.value,
      },
    });
  };

  const handleChangeChance = ({ target }: ChangeEvent<HTMLInputElement>) => {
    controlDispatch({
      type: "changeValue",
      value: {
        element: "chance",
        amount: target.value,
      },
    });
  };

  const onBlur = () => {
    controlDispatch({
      type: "calculateOdds",
    });
  };

  return (
    <div className={css.box}>
      <div>
        <h1 className={css.title}>
          {intl.formatMessage({
            id: "dice.manual.base.bet",
          })}
        </h1>
        <Amount />
      </div>
      <div className={css.elements}>
        <div>
          <h1 className={css.title}>
            {intl.formatMessage({
              id: "dice.manual.base.profit",
            })}
          </h1>

          <div className={css.inputWrapper}>
            <Input
              withCoin
              disabled
              value={
                showInUSD && controlState.general.activeCoin.name !== "GEM"
                  ? `$${controlState.panel.main.profit}`
                  : controlState.panel.main.profit
              }
              type="text"
            />
            <GetCoinImg />
          </div>
        </div>

        <div className={css.payoutElements}>
          <div>
            <h1 className={css.title}>
              {intl.formatMessage({
                id: "dice.manual.base.odds",
              })}
            </h1>

            <div className={css.inputWrapper}>
              <Input
                value={controlState.panel.main.odds}
                onChange={handleChangeOdds}
                onBlur={onBlur}
                disabled={controlState.auto.isStarted}
              />
            </div>
          </div>

          <div>
            <h1 className={css.title}>
              {intl.formatMessage({
                id: "dice.manual.base.chance",
              })}
            </h1>

            <div className={clsx(css.inputWrapper, css.percentWrapper)}>
              <Input
                className={css.inputPercent}
                value={controlState.panel.main.chance}
                onChange={handleChangeChance}
                onBlur={onBlur}
                disabled={controlState.auto.isStarted}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Manual;
