import React, { useEffect, useLayoutEffect, useRef } from "react";

import { useChat, useWindowSize } from "providers";
import { useDocumentLoaded } from "hooks";
import { createTimeouts } from "utils";

import { useWhiteTheme } from "providers/WhiteTheme";

import { BoardParamsType } from "../Panel/components/Listener/types";
import Roller from "./Roller/Roller";
import css from "./style.module.scss";

interface PanelProps {
  params: BoardParamsType;
}

const { clearTimeouts, pushTimeout } = createTimeouts();

export const Board = ({ params: board }: PanelProps) => {
  const { isWhiteThemeActive } = useWhiteTheme();
  const { width } = useWindowSize();
  const { isHide } = useChat();
  const isLoad = useDocumentLoaded();

  const mainRef = useRef<HTMLDivElement | null>(null);
  const includeRef = useRef<HTMLDivElement | null>(null);
  const boxRef = useRef<HTMLDivElement>(null);

  const scale = () => {
    if (mainRef.current && includeRef.current) {
      const zoom = mainRef.current.clientWidth / includeRef.current.clientWidth;
      includeRef.current.style.transform = `scale(${zoom.toFixed(2)})`;

      if (boxRef.current) {
        boxRef.current.style.height = `${
          includeRef.current.offsetHeight * zoom * (zoom < 0.5 ? 1.2 : 1)
        }px`;
      }
    }
  };

  useLayoutEffect(() => {
    pushTimeout(() => scale());
  }, [width, isHide, isLoad]);

  useEffect(() => {
    return () => {
      clearTimeouts();
    };
  }, []);

  return (
    <div
      className={css.betBox}
      ref={(node) => {
        mainRef.current = node;
        pushTimeout(() => scale());
      }}>
      <div className={css.panelBox} ref={boxRef}>
        <div className={css.range}>
          <div
            className={`${css.rangeBox} ${
              board.type === 1 ? css.rangeMin : css.rangeMax
            }`}>
            <p className={css.rangeText}>
              {board.type === 1
                ? "< " + board.range.min
                : board.range.max + " <"}
            </p>
          </div>
        </div>
        <div
          className={css.dataWrapper}
          ref={(node) => {
            includeRef.current = node;
            pushTimeout(() => scale());
          }}>
          <img
            loading="lazy"
            src={
              !isWhiteThemeActive
                ? "assets/dice/img/darkPanel.png"
                : "assets/dice/img/whitePanel.png"
            }
            alt="dice panel"
            className={css.imgPanel}
          />
          <Roller
            num={board.value}
            isFail={board.isFail}
            isAuto={board.isAuto}
          />
        </div>
      </div>
    </div>
  );
};

export default React.memo(Board);
