import { DiceControlActions, DiceControlState } from "../types";

export default function setCoinConfig(
  state: DiceControlState,
  action: DiceControlActions,
) {
  switch (action.type) {
    case "setGeneralCoinData":
      const selectedGeneralCoin = action.coins.find(
        (coin) => coin.coin === state.general.activeCoin.name,
      );
      return {
        allCoins: action.coins,
        selectedCoin:
          selectedGeneralCoin || state.general.coinsConfig.selectedCoin,
      };

    case "changeCoinData":
      const selectedChangeCoin = state.general.coinsConfig.allCoins.find(
        (coin) => action.newCoin === coin.coin,
      );

      return {
        allCoins: state.general.coinsConfig.allCoins,
        selectedCoin:
          selectedChangeCoin || state.general.coinsConfig.selectedCoin,
      };

    default:
      break;
  }
  return state.general.coinsConfig;
}
