import React, { Dispatch } from "react";
import { initialDiceControlState } from "./state";
import { DiceControlActions, DiceControlState } from "./types";

type DiceControlContext = {
  controlState: DiceControlState;
  controlDispatch: Dispatch<DiceControlActions>;
};

export const defaultValue: DiceControlContext = {
  controlState: initialDiceControlState,
  controlDispatch: () => {},
};

export const Context = React.createContext<DiceControlContext>(defaultValue);

export default Context;
