import React, { FC } from "react";

import { useDiceControl } from "games/Dice/components/Reducer";

import { Manual } from "./components";
import Auto from "./components/Auto";

const Params: FC = () => {
  const { controlState } = useDiceControl();

  return (
    <>
      <Manual />
      {controlState.auto.isOpen && <Auto />}
    </>
  );
};

export default Params;
