import { toDecimal } from "utils";
import { Decimal } from "utils/decimal";
import { diceControlConfig as config } from "../config";
import { DiceControlActions, DiceControlState } from "../types";
import { calculateProfit } from "./calculates";

const isPnlOverLimit = (
  pnlAmount: Decimal,
  stopOnValueString: string,
  type: "loss" | "profit",
) => {
  const stopOn = Number(stopOnValueString);
  const pnl = type === "loss" ? pnlAmount.mul(-1) : pnlAmount;
  if (
    !isNaN(stopOn) &&
    isFinite(stopOn) &&
    stopOnValueString !== "" &&
    stopOn > 0 &&
    !pnl.eq(0)
  ) {
    return pnl.gte(stopOn);
  }
  return false;
};

export default function autoResponse(
  state: DiceControlState,
  action: DiceControlActions,
  profit: string,
): DiceControlState {
  const auto = {
    ...state.auto,
  };
  const manual = {
    ...state.panel.main,
  };
  const general = {
    ...state.general,
  };
  let isReady = true;

  switch (action.type) {
    case "boardInstall":
      const decisions = auto.decisions[action.value.win ? "win" : "lose"];

      if (state.auto.isStarted) {
        switch (decisions.variant) {
          case 0:
            auto.isStarted = false;
            isReady = false;
            break;
          case 1:
            manual.betAmount = state.auto.baseBet;
            break;
          case 2:
            const betCalculation =
              Number(manual.betAmount) +
              (Number(manual.betAmount) * Number(decisions.betPercent)) / 100;

            if (
              betCalculation >
              Number(state.general.coinsConfig.selectedCoin.maximum_bet)
            ) {
              manual.betAmount =
                state.general.coinsConfig.selectedCoin.maximum_bet;
            } else if (
              betCalculation <
              Number(state.general.coinsConfig.selectedCoin.minimum_bet)
            ) {
              manual.betAmount =
                state.general.coinsConfig.selectedCoin.minimum_bet;
            } else {
              manual.betAmount = betCalculation.toFixed(config.accuracy);
            }
            break;
          default:
            break;
        }

        if (decisions.isCashout) {
          manual.chance = decisions.percent;
          manual.odds = decisions.cashout;
        }

        const initialType = general.board.type;
        switch (state.auto.selectType) {
          case "0":
            if (action.value.win) {
              general.board.type = initialType === 1 ? 2 : 1;
            }
            break;

          case "1":
            if (!action.value.win) {
              general.board.type = initialType === 1 ? 2 : 1;
            }
            break;

          case "2":
            general.board.type = initialType === 1 ? 2 : 1;
            break;

          case "3":
          default:
            break;
        }
        if (auto.numberOfBets.expected !== 0) {
          auto.numberOfBets.current += 1;
          if (auto.numberOfBets.expected - auto.numberOfBets.current < 0) {
            auto.isStarted = false;
            isReady = false;
          }
        }

        auto.pnl = auto.pnl.add(profit);
        if (
          isPnlOverLimit(auto.pnl, auto.stopOnLoss, "loss") ||
          isPnlOverLimit(auto.pnl, auto.stopOnProfit, "profit")
        ) {
          auto.isStarted = false;
          isReady = false;
        }

        if (!auto.isStarted && !isReady) {
          auto.pnl = toDecimal(0);
        }

        manual.profit = calculateProfit(manual.betAmount, manual.odds);
        auto.isReady = isReady;
      }
      break;

    default:
      break;
  }
  return {
    auto,
    general,
    panel: {
      main: manual,
    },
  };
}
